.styled-select {
  display:inline-block;
  background-color: white;
  color: $grey;
  cursor:pointer;
  font-size:1.1em;
  font-weight: 600;
  margin-bottom:1.5em;
  padding:.3em;
  position:relative;
  border: solid 1px $grey;
  border-radius: $radius;
  height: 2.3em;
  width:8em;
}

.styled-select:before {
  font-family: "FontAwesome";
  border-width: 0 0 0 1px;
  content:'\f103';
  color:$color-primary;
  font-size:1.1em;
  padding-left:.25em;
  position:absolute;
  pointer-events:none;
  right:.35em;
}
#pages, select{
  appearance:none;
  -moz-appearance:none;
  -webkit-appearance:none;
  cursor:pointer;
}
#pages {
  position:absolute;
  top:2.3em;
  left:0px;
  z-index: 100;
  display:none;
  width:8em;
  margin:1px;
  padding:0 2em;
  background-color:white;
  border:none;
  color: $font-color;
  font-family: $font;
  font-size:.8em;
  border:1px solid $color-bg;
  @include shadow;
  outline-color: rgba(255,99,71,0.2);
}

.selectedPage{
  padding-left:1em;
  margin:0;
  font-family: $font;
  font-size: .75em;
  font-weight: 400;
}

//************ select country ***********/
select{
  display:inline-block;
  width:100%;
  margin-top:10px;
  padding:15px;
  border:1px solid $color-primary;
  border-radius: $radius;
  background-color: white;
  font-size: .8em;
  font-weight: 700;
  color:$color-primary;
  text-decoration: none;
  text-align: center;
  transition: background-color .5s;
  -webkit-transition:background-color .5s;
  @media #{$mobile-l}{
    padding: 5px 10px;
  }
  &:hover{
    background-color: white;
    color: $color-primary;
  }
  .fa {
    font-size: 1.2em;
    padding-right: 5px;
    padding-left: 10px;
  }
}
